// hooks/useEsia.ts
import { useCallback, useState } from 'react';
import CryptoJS from 'crypto-js';

const ESIA_BASE_URL = 'https://esia.gosuslugi.ru';
const ESIA_AUTH_URL = `${ESIA_BASE_URL}/aas/oauth2/ac`;
const ESIA_TOKEN_URL = `${ESIA_BASE_URL}/aas/oauth2/te`;
const ESIA_USERINFO_URL = `${ESIA_BASE_URL}/rs/prns`;

export const useEsia = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [userInfo, setUserInfo] = useState<any>(null);

  //  state  code_verifier  PKCE
  const generateRandomString = useCallback((length: number): string => {
    const possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789-._~';
    let text = '';
    for (let i = 0; i < length; i++) {
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    }
    return text;
  }, []);

  //  code_challenge  PKCE
  const generateCodeChallenge = useCallback((codeVerifier: string): string => {
    return CryptoJS.SHA256(codeVerifier).toString(CryptoJS.enc.Base64url);
  }, []);

  const initiateAuth = useCallback(() => {
    setIsLoading(true);
    setError(null);

    try {
      const state = generateRandomString(32);
      const codeVerifier = generateRandomString(64);
      const codeChallenge = generateCodeChallenge(codeVerifier);
      const timestamp = new Date().toISOString().replace(/[-:.]/g, '').slice(0, 15);

      const config = {
        clientId: import.meta.env.VITE_ESIA_CLIENT_ID || 'TEST_CLIENT',
        redirectUri: `${window.location.origin}/auth/callback`,
        scope: 'openid fullname birthdate email mobile snils inn',
        state,
        accessType: 'online',
        responseType: 'code',
        timestamp,
        codeChallenge,
        codeChallengeMethod: 'S256',
      };

      //  code_verifier   
      sessionStorage.setItem('esia_code_verifier', codeVerifier);
      sessionStorage.setItem('esia_state', state);
      sessionStorage.setItem('esia_timestamp', timestamp);

      const params = new URLSearchParams({
        client_id: config.clientId,
        client_secret: import.meta.env.VITE_ESIA_CLIENT_SECRET || 'TEST_SECRET',
        redirect_uri: config.redirectUri,
        scope: config.scope,
        response_type: config.responseType,
        state: config.state,
        timestamp: config.timestamp,
        access_type: config.accessType,
        code_challenge: config.codeChallenge,
        code_challenge_method: config.codeChallengeMethod,
      });

      const authUrl = `${ESIA_AUTH_URL}?${params.toString()}`;

      console.log('Redirecting to ESIA:', authUrl);
      window.location.href = authUrl;
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : '  ';
      setError(errorMessage);
      setIsLoading(false);
    }
  }, [generateRandomString, generateCodeChallenge]);

  const exchangeCodeForToken = useCallback(async (code: string): Promise<any> => {
    setIsLoading(true);

    try {
      const codeVerifier = sessionStorage.getItem('esia_code_verifier');
      const state = sessionStorage.getItem('esia_state');
      const timestamp = sessionStorage.getItem('esia_timestamp');

      if (!codeVerifier) {
        throw new Error('Code verifier not found');
      }

      const params = new URLSearchParams({
        client_id: import.meta.env.VITE_ESIA_CLIENT_ID || 'TEST_CLIENT',
        client_secret: import.meta.env.VITE_ESIA_CLIENT_SECRET || 'TEST_SECRET',
        code,
        grant_type: 'authorization_code',
        redirect_uri: `${window.location.origin}/auth/callback`,
        code_verifier: codeVerifier,
        timestamp: timestamp || new Date().toISOString().replace(/[-:.]/g, '').slice(0, 15),
        token_type: 'Bearer',
        state: state || '',
      });

      const response = await fetch(ESIA_TOKEN_URL, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: params.toString(),
      });

      if (!response.ok) {
        throw new Error(`Token request failed: ${response.status}`);
      }

      const tokenData = await response.json();
      return tokenData;
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : '  ';
      setError(errorMessage);
      throw err;
    } finally {
      setIsLoading(false);
    }
  }, []);

  const getUserInfo = useCallback(async (accessToken: string, userId: string): Promise<any> => {
    setIsLoading(true);

    try {
      const response = await fetch(`${ESIA_USERINFO_URL}/${userId}`, {
        method: 'GET',
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      });

      if (!response.ok) {
        throw new Error(`User info request failed: ${response.status}`);
      }

      const userData = await response.json();
      setUserInfo(userData);
      return userData;
    } catch (err) {
      const errorMessage =
        err instanceof Error ? err.message : '   ';
      setError(errorMessage);
      throw err;
    } finally {
      setIsLoading(false);
    }
  }, []);

  const handleCallback = useCallback(
    async (urlParams: URLSearchParams): Promise<any> => {
      const code = urlParams.get('code');
      const state = urlParams.get('state');
      const error = urlParams.get('error');
      const errorDescription = urlParams.get('error_description');

      //  state   CSRF
      const savedState = sessionStorage.getItem('esia_state');
      if (state !== savedState) {
        throw new Error('State parameter mismatch');
      }

      if (error) {
        throw new Error(`${error}: ${errorDescription}`);
      }

      if (!code) {
        throw new Error('No authorization code received');
      }

      const tokenData = await exchangeCodeForToken(code);

      //  ID   access_token (JWT)
      const tokenParts = tokenData.access_token.split('.');
      if (tokenParts.length !== 3) {
        throw new Error('Invalid access token format');
      }

      const payload = JSON.parse(atob(tokenParts[1]));
      const userId = payload.sub;

      const userData = await getUserInfo(tokenData.access_token, userId);

      //  sessionStorage
      sessionStorage.removeItem('esia_code_verifier');
      sessionStorage.removeItem('esia_state');
      sessionStorage.removeItem('esia_timestamp');

      return { tokenData, userData };
    },
    [exchangeCodeForToken, getUserInfo],
  );

  return {
    isLoading,
    error,
    userInfo,
    initiateAuth,
    exchangeCodeForToken,
    getUserInfo,
    handleCallback,
  };
};
