// pages/EsiaCallbackPage.tsx
import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { useEsia } from '@/hooks/useEsia';
import { LoadingSpinner } from '@/components/ui/StyledComponents';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
`;

const Card = styled.div`
  background: white;
  border-radius: 16px;
  padding: 40px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  max-width: 480px;
  width: 100%;
  text-align: center;
`;

const Title = styled.h1`
  color: #333;
  margin-bottom: 16px;
`;

const Message = styled.p`
  color: #666;
  margin-bottom: 24px;
  line-height: 1.5;
`;

const ErrorMessage = styled.div`
  background: #fee;
  border: 1px solid #f5c6cb;
  border-radius: 8px;
  padding: 16px;
  margin: 16px 0;
  color: #721c24;
  text-align: left;
`;

const RetryButton = styled.button`
  background: #2d5bff;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 12px 24px;
  font-size: 14px;
  cursor: pointer;
  margin-top: 16px;

  &:hover {
    background: #1a4aff;
  }
`;

export const EsiaCallbackPage: React.FC = () => {
  const navigate = useNavigate();
  const { handleCallback, isLoading } = useEsia();
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const processCallback = async () => {
      try {
        const urlParams = new URLSearchParams(window.location.search);
        await handleCallback(urlParams);

        //       
        setTimeout(() => {
          navigate('/');
        }, 1000);
      } catch (err) {
        setError(err instanceof Error ? err.message : '  ');
      }
    };

    processCallback();
  }, [handleCallback, navigate]);

  const handleRetry = () => {
    navigate('/auth');
  };

  return (
    <Container>
      <Card>
        {isLoading && !error && (
          <>
            <LoadingSpinner size={32} />
            <Title> </Title>
            <Message>
              , .       ...
            </Message>
          </>
        )}

        {error && (
          <>
            <Title> </Title>
            <ErrorMessage>
              <strong> :</strong> {error}
            </ErrorMessage>
            <RetryButton onClick={handleRetry}> </RetryButton>
          </>
        )}

        {!isLoading && !error && (
          <>
            <Title> </Title>
            <Message>
                  .    ...
            </Message>
            <LoadingSpinner size={24} />
          </>
        )}
      </Card>
    </Container>
  );
};
